.main_wrapper {
    background: rgb(44, 44, 44);

    height: 100vh;
    width: 100vw;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 10000;
    overflow: hidden;
    .main_content_wrapper {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        overflow-y: scroll;
    }
}
