.form_wrapper {
    width: 100%;
    height: 100%;
    padding: 0;

    .top_wrapper {
        height: 50px;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;

        background: rgba(0, 48, 169, 0.89);

        .btn {
            height: 50px;
            width: 50px;
            background: transparent;
            cursor: pointer;

            border: none;
            outline: none;
        }
    }

    .errorMessageWrapper {
        display: flex;
        flex-direction: column;

        .errorMessage {
            border: 2px solid rgb(170, 0, 0);
            color: rgb(170, 0, 0);
            text-shadow: 0 0 5px rgb(68, 146, 139);
            box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.872);
            box-sizing: border-box;
            padding: 5px 10px;
            margin: 15px 5px;
        }
    }
}
